@charset "utf-8";

// 1. Import the initial variables
@import "../../node_modules/bulma/sass/utilities/initial-variables";

$family-sans-serif: "Helvetica", "Arial", sans-serif;
$box-background-color: #525252;
//$box-padding: 5rem;

// 4. Import the rest of Bulma
@import "../../node_modules/bulma/bulma";

// icons
@import "../../node_modules/@mdi/font/scss/materialdesignicons";

// 5. normal css
 
body,
html {
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}

body {
  background-color: #666666;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#options {
  background-color: $box-background-color;
  border-radius: 10px;
  padding: 2rem;
}

#buttons-list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
button {
  margin-top: 10px;
}
